import React from 'react';
import { Jumbotron as Jumbo, Container } from 'react-bootstrap';
import second from '../slider photo/second.jpeg';
import styled from 'styled-components';
import { createBootstrapComponent } from 'react-bootstrap/esm/ThemeProvider';

const Styles = styled.div`
.jumbo{
background : url(${second}) no-repeat fixed bottom;
background-size: cover;
color: #efefef;
height: 400px;
position: relative;
z-index: -2
}
.overlay{
    background-color:#000;
    opacity:0.5;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
}

`;


const Jumbotron = () => (
    <Styles>
        <Jumbo fluid className="jumbo">
            <div classNamve="overLay"></div>
            <Container>
                <h1>Welcome to my blog</h1>
                <p>Lorem Ipsum является текст-заполнитель обычно используется
                в графических, печать и издательской индустрии
                для предварительного
                просмотра макета и визуальных макетах.
                Tекст-заполнитель обычно используется
                в графических, печать и издательской индустрии
                для предварительного
                просмотра макета и визуальных макетах.
                Tекст-заполнитель обычно используется
                в графических, печать и издательской индустрии
                для предварительного
                просмотра макета и визуальных макетах.</p>
            </Container>
        </Jumbo>
    </Styles>
)

export default Jumbotron;